/* Base Styles */
html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  overflow: hidden;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #ffffff;
  color: #374151;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.App {
  min-height: 100vh;
  background: #ffffff;
  display: flex;
  padding: 0;
  width: 100%;
  height: 100vh;
}

.App:has(.chatgpt-layout) {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

/* Login Styles */
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  background: #ffffff;
  width: 100%;
}

.login-card {
  background: #ffffff;
  border: 1px solid #e5e7eb;
  border-radius: 12px;
  padding: 40px;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  width: 100%;
  max-width: 400px;
  position: relative;
}

.login-header {
  text-align: center;
  margin-bottom: 30px;
}

.login-header h1 {
  color: #111827;
  font-size: 28px;
  margin-bottom: 10px;
  font-weight: 600;
}

.login-header p {
  color: #6b7280;
  font-size: 16px;
}

.login-form {
  position: relative;
}

.form-group {
  margin-bottom: 20px;
}

.form-group label {
  display: block;
  color: #374151;
  margin-bottom: 8px;
  font-size: 14px;
  font-weight: 500;
}

.form-group input {
  width: 100%;
  padding: 12px 16px;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  background: #ffffff;
  color: #111827;
  font-size: 16px;
  transition: all 0.2s ease;
}

.form-group input:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.form-group input::placeholder {
  color: #9ca3af;
}

.login-btn {
  width: 100%;
  padding: 12px 16px;
  background: #3b82f6;
  border: 1px solid #3b82f6;
  border-radius: 8px;
  color: #ffffff;
  font-size: 16px;
  font-weight: 500;
  cursor: pointer;
  transition: all 0.2s ease;
}

.login-btn:hover:not(:disabled) {
  background: #2563eb;
  border-color: #2563eb;
}

.login-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.error-message {
  background: #fef2f2;
  border: 1px solid #fecaca;
  color: #dc2626;
  padding: 12px;
  border-radius: 8px;
  margin-top: 15px;
  text-align: center;
  font-size: 14px;
}

.demo-credentials {
  text-align: center;
  margin-top: 20px;
}

.demo-credentials p {
  color: #6b7280;
  font-size: 12px;
}

/* Language Selector Styles */
.language-selector-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.language-selector-modal {
  background: #ffffff;
  border: 1px solid #e5e7eb;
  border-radius: 12px;
  padding: 30px;
  max-width: 500px;
  width: 90%;
  max-height: 80vh;
  overflow-y: auto;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

.language-selector-header {
  text-align: center;
  margin-bottom: 30px;
}

.language-selector-header h2 {
  color: #111827;
  font-size: 24px;
  margin-bottom: 8px;
}

.language-selector-header p {
  color: #6b7280;
  font-size: 16px;
}

.language-grid {
  display: grid;
  gap: 12px;
}

.language-option {
  background: #ffffff;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  padding: 16px 20px;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.language-option:hover {
  background: #f9fafb;
  border-color: #d1d5db;
}

.language-info {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.language-name {
  color: #111827;
  font-weight: 600;
  font-size: 16px;
}

.language-native {
  color: #6b7280;
  font-size: 14px;
}

.language-arrow {
  color: #9ca3af;
  font-size: 18px;
  transition: transform 0.2s ease;
}

.language-option:hover .language-arrow {
  transform: translateX(5px);
}

/* Settings Styles */
.settings-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

.settings-modal {
  background: #ffffff;
  border: 1px solid #e5e7eb;
  border-radius: 12px;
  max-width: 600px;
  width: 90%;
  max-height: 80vh;
  overflow-y: auto;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

.settings-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px 30px;
  border-bottom: 1px solid #e5e7eb;
}

.settings-header h2 {
  color: #111827;
  font-size: 20px;
  font-weight: 600;
}

.settings-close {
  background: none;
  border: none;
  color: #6b7280;
  font-size: 20px;
  cursor: pointer;
  padding: 8px;
  border-radius: 6px;
  transition: all 0.2s ease;
}

.settings-close:hover {
  background: #f3f4f6;
  color: #374151;
}

.settings-tabs {
  display: flex;
  border-bottom: 1px solid #e5e7eb;
}

.settings-tab {
  flex: 1;
  padding: 16px 20px;
  background: none;
  border: none;
  color: #6b7280;
  cursor: pointer;
  transition: all 0.2s ease;
  font-size: 14px;
  font-weight: 500;
}

.settings-tab.active {
  color: #3b82f6;
  background: #eff6ff;
  border-bottom: 2px solid #3b82f6;
}

.settings-tab:hover {
  background: #f9fafb;
  color: #374151;
}

.settings-content {
  padding: 30px;
}

.settings-section h3 {
  color: #111827;
  font-size: 18px;
  margin-bottom: 20px;
  font-weight: 600;
}

.language-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.language-item {
  background: #ffffff;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  padding: 16px 20px;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.language-item:hover {
  background: #f9fafb;
  border-color: #d1d5db;
}

.language-item.selected {
  background: #eff6ff;
  border-color: #3b82f6;
}

.language-item-info {
  display: flex;
  flex-direction: column;
  gap: 4px;
}

.language-item-name {
  color: #111827;
  font-weight: 600;
  font-size: 16px;
}

.language-item-native {
  color: #6b7280;
  font-size: 14px;
}

.language-item-check {
  color: #10b981;
  font-size: 18px;
  font-weight: bold;
}

.settings-actions {
  display: flex;
  gap: 12px;
  margin-top: 20px;
}

.settings-save-btn {
  padding: 12px 24px;
  background: #3b82f6;
  border: 1px solid #3b82f6;
  border-radius: 8px;
  color: #ffffff;
  cursor: pointer;
  transition: all 0.2s ease;
  font-weight: 500;
}

.settings-save-btn:hover:not(:disabled) {
  background: #2563eb;
  border-color: #2563eb;
}

.settings-save-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

.settings-cancel-btn {
  padding: 12px 24px;
  background: #ffffff;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  color: #374151;
  cursor: pointer;
  transition: all 0.2s ease;
  font-weight: 500;
}

.settings-cancel-btn:hover {
  background: #f9fafb;
  border-color: #9ca3af;
}

.settings-message {
  margin-top: 15px;
  padding: 12px;
  border-radius: 8px;
  text-align: center;
  font-size: 14px;
}

.settings-message.success {
  background: #f0fdf4;
  border: 1px solid #bbf7d0;
  color: #166534;
}

.settings-message.error {
  background: #fef2f2;
  border: 1px solid #fecaca;
  color: #dc2626;
}

/* Link button styles */
.link-btn {
  background: none;
  border: none;
  color: #3b82f6;
  text-decoration: underline;
  cursor: pointer;
  font-size: inherit;
  padding: 0;
  margin: 0;
  transition: color 0.2s ease;
}

.link-btn:hover {
  color: #2563eb;
}

/* Login footer styles */
.login-footer {
  text-align: center;
  margin-top: 20px;
  padding-top: 20px;
  border-top: 1px solid #e5e7eb;
}

.login-footer p {
  color: #6b7280;
  font-size: 14px;
  margin: 0;
}

/* ChatGPT-like Layout */
.chatgpt-layout {
  display: flex;
  height: 100vh;
  width: 100vw;
  background: #ffffff;
  color: #374151;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

/* Sidebar Styles */
.sidebar {
  width: 260px;
  background: #f7f7f8;
  border-right: 1px solid #e5e7eb;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.sidebar-header {
  padding: 16px;
  border-bottom: 1px solid #e5e7eb;
}

.new-chat-btn {
  width: 100%;
  padding: 12px 16px;
  background: #ffffff;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  color: #374151;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  gap: 8px;
  font-weight: 500;
}

.new-chat-btn:hover {
  background: #f9fafb;
  border-color: #d1d5db;
}

.new-chat-btn span {
  font-size: 16px;
  font-weight: bold;
  color: #3b82f6;
}

.conversations-list {
  flex: 1;
  overflow-y: auto;
  padding: 8px;
}

.conversation-item {
  padding: 12px 16px;
  margin: 2px 0;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.conversation-item:hover {
  background: #f3f4f6;
}

.conversation-item.active {
  background: #eff6ff;
  border: 1px solid #3b82f6;
}

.conversation-content {
  flex: 1;
  min-width: 0;
}

.conversation-title {
  color: #111827;
  font-size: 14px;
  font-weight: 500;
  margin-bottom: 2px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.conversation-meta {
  color: #6b7280;
  font-size: 12px;
}

.active-indicator {
  color: #3b82f6;
  font-size: 12px;
}

.sidebar-footer {
  padding: 16px;
  border-top: 1px solid #e5e7eb;
}

.user-info {
  display: flex;
  align-items: center;
  gap: 12px;
  margin-bottom: 16px;
}

.user-avatar {
  width: 32px;
  height: 32px;
  background: #3b82f6;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  color: #ffffff;
  font-weight: 600;
  font-size: 14px;
}

.user-details {
  flex: 1;
  min-width: 0;
}

.user-name {
  color: #111827;
  font-size: 14px;
  font-weight: 500;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.user-email {
  color: #6b7280;
  font-size: 12px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.sidebar-actions {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.sidebar-action-btn {
  width: 100%;
  padding: 8px 12px;
  background: transparent;
  border: 1px solid #e5e7eb;
  border-radius: 6px;
  color: #374151;
  font-size: 12px;
  cursor: pointer;
  transition: all 0.2s ease;
  text-align: left;
}

.sidebar-action-btn:hover {
  background: #f3f4f6;
  border-color: #d1d5db;
}

.sidebar-action-btn.logout {
  color: #dc2626;
  border-color: #fecaca;
}

.sidebar-action-btn.logout:hover {
  background: #fef2f2;
  border-color: #fca5a5;
}

/* Main Chat Area */
.main-chat {
  flex: 1;
  display: flex;
  flex-direction: column;
  background: #ffffff;
  width: calc(100vw - 260px);
  min-width: 0;
}

.chat-header {
  padding: 16px 24px;
  border-bottom: 1px solid #e5e7eb;
  background: #ffffff;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.chat-header-content h1 {
  color: #111827;
  font-size: 20px;
  font-weight: 600;
  margin: 0 0 4px 0;
}

.chat-header-content p {
  color: #6b7280;
  font-size: 14px;
  margin: 0;
}

.chat-header-actions {
  display: flex;
  gap: 8px;
}

.header-action-btn {
  padding: 8px 16px;
  background: transparent;
  border: 1px solid #e5e7eb;
  border-radius: 6px;
  color: #374151;
  font-size: 12px;
  cursor: pointer;
  transition: all 0.2s ease;
}

.header-action-btn:hover {
  background: #f9fafb;
  border-color: #d1d5db;
}

.header-action-btn.active {
  background: #eff6ff;
  border-color: #3b82f6;
  color: #3b82f6;
}

/* Messages Container */
.messages-container {
  flex: 1;
  overflow-y: auto;
  padding: 0;
  background: #ffffff;
}

.messages-container::-webkit-scrollbar {
  width: 6px;
}

.messages-container::-webkit-scrollbar-track {
  background: transparent;
}

.messages-container::-webkit-scrollbar-thumb {
  background: #d1d5db;
  border-radius: 3px;
}

.messages-container::-webkit-scrollbar-thumb:hover {
  background: #9ca3af;
}

/* Empty State */
.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100%;
  text-align: center;
  padding: 40px;
}

.empty-state-icon {
  font-size: 48px;
  margin-bottom: 24px;
}

.empty-state h2 {
  color: #111827;
  font-size: 24px;
  font-weight: 600;
  margin: 0 0 16px 0;
}

.empty-state p {
  color: #6b7280;
  font-size: 16px;
  line-height: 1.5;
  max-width: 500px;
  margin: 0;
}

/* Message Styles */
.message {
  display: flex;
  padding: 20px 24px;
  border-bottom: 1px solid #f3f4f6;
}

.message.user {
  background: #ffffff;
}

.message.ai {
  background: #f9fafb;
}

.message-avatar {
  width: 30px;
  height: 30px;
  border-radius: 4px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 16px;
  margin-right: 16px;
  flex-shrink: 0;
}

.message-content {
  flex: 1;
  max-width: calc(100% - 46px);
}

.user-message {
  color: #111827;
  font-size: 16px;
  line-height: 1.6;
  white-space: pre-wrap;
}

.ai-message {
  color: #374151;
  font-size: 16px;
  line-height: 1.6;
}

.ai-message h1,
.ai-message h2,
.ai-message h3 {
  color: #111827;
  margin: 8px 0;
}

.ai-message p {
  color: #374151;
  margin: 4px 0;
}

.ai-message ul,
.ai-message ol {
  margin: 8px 0;
  padding-left: 20px;
}

.ai-message li {
  margin: 2px 0;
  color: #374151;
}

.ai-message strong {
  color: #111827;
  font-weight: 600;
}

.ai-message em {
  color: #6b7280;
  font-style: italic;
}

.ai-message blockquote {
  border-left: 3px solid #3b82f6;
  padding-left: 12px;
  margin: 8px 0;
  font-style: italic;
  color: #6b7280;
}

.ai-message code {
  background: #f3f4f6;
  padding: 2px 6px;
  border-radius: 4px;
  font-family: 'Monaco', 'Menlo', 'Ubuntu Mono', monospace;
  color: #111827;
}

.message-image {
  max-width: 200px;
  max-height: 200px;
  border-radius: 8px;
  margin-top: 8px;
}

/* Typing Indicator */
.typing-indicator {
  display: flex;
  gap: 4px;
  align-items: center;
}

.typing-indicator span {
  width: 8px;
  height: 8px;
  background: #3b82f6;
  border-radius: 50%;
  animation: typing 1.4s infinite ease-in-out;
}

.typing-indicator span:nth-child(1) {
  animation-delay: -0.32s;
}

.typing-indicator span:nth-child(2) {
  animation-delay: -0.16s;
}

@keyframes typing {
  0%, 80%, 100% {
    transform: scale(0.8);
    opacity: 0.5;
  }
  40% {
    transform: scale(1);
    opacity: 1;
  }
}

/* Input Container */
.input-container {
  padding: 20px 24px;
  background: #ffffff;
  border-top: 1px solid #e5e7eb;
}

.input-wrapper {
  display: flex;
  gap: 12px;
  align-items: flex-end;
  max-width: 768px;
  margin: 0 auto;
}

.text-input {
  flex: 1;
  border: 1px solid #d1d5db;
  border-radius: 8px;
  background: #ffffff;
  color: #111827;
  padding: 12px 16px;
  font-size: 16px;
  resize: none;
  transition: all 0.2s ease;
  min-height: 52px;
  max-height: 200px;
  font-family: inherit;
}

.text-input:focus {
  outline: none;
  border-color: #3b82f6;
  box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
}

.text-input::placeholder {
  color: #9ca3af;
}

.input-actions {
  display: flex;
  gap: 8px;
  align-items: flex-end;
}

.upload-btn {
  background: transparent;
  border: 1px solid #d1d5db;
  color: #6b7280;
  padding: 12px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 16px;
  transition: all 0.2s ease;
  width: 48px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.upload-btn:hover {
  background: #f9fafb;
  border-color: #9ca3af;
  color: #374151;
}

.send-btn {
  background: #3b82f6;
  border: 1px solid #3b82f6;
  color: #ffffff;
  padding: 12px 20px;
  border-radius: 8px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 500;
  transition: all 0.2s ease;
  min-width: 80px;
}

.send-btn:hover:not(:disabled) {
  background: #2563eb;
  border-color: #2563eb;
}

.send-btn:disabled {
  opacity: 0.6;
  cursor: not-allowed;
}

/* Selected Image */
.selected-image {
  margin-top: 12px;
  display: flex;
  align-items: center;
  gap: 12px;
  max-width: 768px;
  margin-left: auto;
  margin-right: auto;
}

.preview-image {
  max-width: 100px;
  max-height: 100px;
  border-radius: 8px;
  border: 1px solid #e5e7eb;
}

.remove-image-btn {
  background: #ef4444;
  border: none;
  color: #ffffff;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  cursor: pointer;
  font-size: 12px;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  justify-content: center;
}

.remove-image-btn:hover {
  background: #dc2626;
}

/* Prescription Form */
.prescription-form {
  padding: 20px 24px;
  background: #f9fafb;
  border-bottom: 1px solid #e5e7eb;
}

.prescription-form h3 {
  color: #111827;
  font-size: 18px;
  font-weight: 600;
  margin: 0 0 16px 0;
}

.prescription-inputs {
  display: flex;
  gap: 16px;
  margin-bottom: 16px;
}

.input-group {
  flex: 1;
}

.input-group label {
  color: #374151;
  font-size: 14px;
  display: block;
  margin-bottom: 6px;
  font-weight: 500;
}

.input-group input {
  width: 100%;
  padding: 10px 12px;
  border: 1px solid #d1d5db;
  border-radius: 6px;
  background: #ffffff;
  color: #111827;
  font-size: 14px;
  transition: all 0.2s ease;
}

