  /* Theme Toggle Styles */
  .appearance-options {
    padding: 16px 0;
  }
  
  .theme-toggle {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 16px;
    background: #f9fafb;
    border: 1px solid #e5e7eb;
    border-radius: 8px;
    margin-bottom: 16px;
  }
  
  .App.dark-mode .theme-toggle {
    background: #404040;
    border-color: #555555;
  }
  
  .theme-info {
    flex: 1;
  }
  
  .theme-name {
    font-weight: 600;
    font-size: 16px;
    color: #111827;
    margin-bottom: 4px;
  }
  
  .App.dark-mode .theme-name {
    color: #ffffff;
  }
  
  .theme-description {
    font-size: 14px;
    color: #6b7280;
  }
  
