  .theme-toggle-btn {
    position: relative;
    width: 48px;
    height: 24px;
    background: #d1d5db;
    border: none;
    border-radius: 12px;
    cursor: pointer;
    transition: all 0.3s ease;
    padding: 2px;
  }
  
  .theme-toggle-btn.light {
    background: #d1d5db;
  }
  
  .theme-toggle-btn.dark {
    background: #3b82f6;
  }
  
  .toggle-slider {
    width: 20px;
    height: 20px;
    background: #ffffff;
    border-radius: 50%;
    transition: all 0.3s ease;
    transform: translateX(0);
  }
  
  .theme-toggle-btn.dark .toggle-slider {
    transform: translateX(24px);
  }
  
  /* Context Menu */
.context-menu {
  background: #ffffff;
  border: 1px solid #e5e7eb;
  border-radius: 8px;
  box-shadow: 0 10px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
  padding: 4px;
  min-width: 150px;
}

.context-menu-item {
  width: 100%;
  padding: 8px 12px;
  background: none;
  border: none;
  text-align: left;
  cursor: pointer;
  font-size: 14px;
  color: #374151;
  border-radius: 4px;
  transition: all 0.2s ease;
  display: flex;
  align-items: center;
  gap: 8px;
}

.context-menu-item:hover {
  background: #f3f4f6;
}

.context-menu-item.delete {
  color: #dc2626;
}

.context-menu-item.delete:hover {
  background: #fef2f2;
  color: #b91c1c;
}

/* Responsive Design */
@media (max-width: 768px) {
  .chatgpt-layout {
    flex-direction: column;
    width: 100vw;
    height: 100vh;
  }
  
  .sidebar {
    width: 100%;
    height: auto;
    max-height: 200px;
    min-height: 200px;
  }
  
  .main-chat {
    flex: 1;
